// React RouterRealmReact RouterRealmMongoDB Atlas App Services
import { RouterProvider, createBrowserRouter } from 'react-router-dom';
import { AppProvider } from '@realm/react';

// 
import { AuthenticatedApp } from './AuthenticatedApp';
import { ErrorPage } from './pages/ErrorPage';
import { LoginPage } from './pages/LoginPage';
import { TaskPage } from './pages/TaskPage';
import config from './atlas-app-services/config.json';
import styles from './styles/App.module.css';

// React Router
const router = createBrowserRouter([
  {
    path: '/', // URLLoginPage
    element: <LoginPage />,
    errorElement: <ErrorPage />, // ErrorPage
  },
  {
    element: <AuthenticatedApp />, // AuthenticatedApp
    children: [
      {
        path: 'tasks', // URL'/tasks'TaskPage
        element: <TaskPage />
      }
    ]
  }
]);

/**
 * React`@realm/react``AppProvider`Atlas App Services
 * `AppProvider`Atlas AppIDIDconfig.json
 * `RouterProvider`React Router
 */
function App() {
  return (
    <div className={styles.container}> 
      <AppProvider id={config.ATLAS_APP_ID}> 
        <RouterProvider router={router} />  
      </AppProvider>
    </div>
  );
}
