// realmResultsrealm
import { Results } from 'realm';

// TaskTask
import type { StickerData } from '../models/Task';
// TaskItem
import { TaskItem } from './TaskItem';
// TaskList
import styles from '../styles/TaskList.module.css';

// TaskListPropsTypeScriptTaskListprops
type TaskListProps = {
  stickers: Results<StickerData>; // Task
  onUpdateSticker: (sticker: StickerData, content: string) => void; // 
  onDeleteTask: (sticker: StickerData) => void; // 
};

/**
 * 
 * @param {TaskListProps} { tasks, onToggleTaskStatus, onDeleteTask } - props
 * @return divTaskItem
 */
export function TaskList({ stickers, onUpdateSticker, onDeleteTask }: TaskListProps) {
  // divstyles.tasks
  return (
    <div className={styles.tasks}>
      {/* tasks.mapTaskItem */}
      {stickers.map((sticker) => (
        <TaskItem
          key={sticker._id} // keyReact_id
          sticker={sticker} // TaskItem
          onUpdateSticker={onUpdateSticker} // TaskItem
          onDelete={onDeleteTask} // TaskItem
        />
      ))}
    </div>
  );
}
