// FIXME: when transport become optional we can use "GenericNetworkTransport | null"

export function platformTransport(): any {
  return createNetworkTransport();
}

const createNetworkTransport: () => any = function () {
  return null;
};

export function inject(injected: () => any) {
  Object.freeze(Object.assign(createNetworkTransport, injected));
}
